
import "./index.scss"
import Head  from "~/components/Head";

import { List } from 'antd-mobile';
import {history} from "&/history";

const Item = List.Item;
const Brief = Item.Brief;

export default class Classify extends Component{

    gotoXianyan=()=>{
        history.push("./xianyan")
    }
    gotoGuyan=()=>{
        history.push("./guyan")
    }
    gotoHuanyan=()=>{
        history.push("./huanyan")
    }
    gotoXiaoyuan=()=>{
        history.push("./xiaoyuan")
    }
    gotoDushi=()=>{
        history.push("./dushi")
    }
    gotoXuanhuan=()=>{
        history.push("./xuanhuan")
    }
    gotoXianxia=()=>{
        history.push("./xianxia")
    }
    gotoYiling=()=>{
        history.push("./yiling")
    }
    gotoLishi=()=>{
        history.push("./lishi")
    }
    gotoYouxi=()=>{
        history.push("./youxi")
    }
    gotoKehuan=()=>{
        history.push("./kehuan")
    }
    gotoWuxia=()=>{
        history.push("./wuxia")
    }
    gotoQihuan=()=>{
        history.push("./qihuan")
    }
    gotoJingji=()=>{
        history.push("./jingji")
    }
    gotoQita=()=>{
        history.push("./qita")
    }


    render(){
        return(
            <div>
                <Head title="分类" show={true}></Head>


                <List renderHeader={() => '女生分类'} className="my-list1">
               
                <Item
                arrow="horizontal"
                thumb
                multipleLine
                onClick={this.gotoXianyan }
                >
                现言 <Brief>豪门总裁 | 婚姻 | 都市情感 | 宝宝</Brief>
                </Item>
                <Item
                arrow="horizontal"
                thumb
                multipleLine
                onClick={this.gotoGuyan }
                >
                古言 <Brief>穿越 | 架空 | 宫斗宅斗 | 情仇爱恨 | 女尊 | 种田</Brief>
                </Item>
                <Item
                arrow="horizontal"
                thumb
                multipleLine
                onClick={this.gotoHuanyan }
                >
                幻言 <Brief>仙侣奇缘 | 江湖奇缘</Brief>
                </Item>
                <Item
                arrow="horizontal"
                thumb
                multipleLine
                onClick={this.gotoXiaoyuan }
                >
                校园 <Brief>青春年华 | 校园魔幻 | 青春校园 | 学生</Brief>
                </Item>
            </List>


            <List renderHeader={() => '男生分类'} className="my-list2">
               
               <Item
               arrow="horizontal"
               thumb
               multipleLine
               onClick={this.gotoDushi }
               >
               都市 <Brief>生活 | 异能 | 重生 | 热血 | 人生 | 美女 | 极道江湖</Brief>
               </Item>
               <Item
               arrow="horizontal"
               thumb
               multipleLine
               onClick={this.gotoXuanhuan }
               >
               玄幻 <Brief>东方玄幻 | 异界大陆 | 转世重生 | 远古神话 </Brief>
               </Item>
               <Item
               arrow="horizontal"
               thumb
               multipleLine
               onClick={this.gotoXianxia }
               >
               仙侠 <Brief>现代修真 | 奇幻修真 | 洪荒封神 | 凡人 | 丹药</Brief>
               </Item>
               <Item
               arrow="horizontal"
               thumb
               multipleLine
               onClick={this.gotoYiling }
               >
               异灵 <Brief>灵异鬼怪 | 恐怖惊悚 | 悬疑探案 | 侦探推理</Brief>
               </Item>
               <Item
               arrow="horizontal"
               thumb
               multipleLine
               onClick={this.gotoLishi }
               >
               历史 <Brief>架空历史 | 两宋元朝 | 秦汉三国 | 权谋 | 乱世</Brief>
               </Item>
               <Item
               arrow="horizontal"
               thumb
               multipleLine
               onClick={this.gotoYouxi }
               >
               游戏 <Brief>虚拟网游 | 游戏异界 | 游戏人生 | 系统</Brief>
               </Item>
               <Item
               arrow="horizontal"
               thumb
               multipleLine
               onClick={this.gotoKehuan }
               >
               科幻 <Brief>未来 | 进化变异 | 末世 | 星际 | 机甲 | 时空穿梭</Brief>
               </Item>
               <Item
               arrow="horizontal"
               thumb
               multipleLine
               onClick={this.gotoWuxia }
               >
               武侠 <Brief>传统武侠 | 新派武侠 | 玄幻异侠 | 江湖风波</Brief>
               </Item>
               <Item
               arrow="horizontal"
               thumb
               multipleLine
               onClick={this.gotoQihuan }
               >
               奇幻 <Brief>领主贵族 | 亡灵异族 | 西方奇幻 | 魔法 | 召唤</Brief>
               </Item>
               <Item
               arrow="horizontal"
               thumb
               multipleLine
               onClick={this.gotoJingji }
               >
               竞技 <Brief>电子竞技 | 体坛风云 | 球类运动 </Brief>
               </Item>
               
               <Item
               arrow="horizontal"
               thumb
               multipleLine
               o onClick={this.gotoQita }
               >
               其他 <Brief>动漫 | 影视 | 武侠 | 小说 </Brief>
               </Item>
           </List>
                
            </div>
        )
    }
}